    <jw-pagination :items="getAllAttendances" @changePage="onChangePage" :labels="customLabels" :styles="customStyles"></jw-pagination>
  </div>
</template>

<script>
  const customLabels = {
    first: '<<',
    last: '>>',
    previous: '<',
    next: '>'
  };

  const customStyles = {
    ul: {
        //border: '2px solid red'
    },
    li: {
        display: 'inline-block',
        //border: '2px dotted green'
    },
    a: {
        color: 'var(--purple-dark)'
    }
  };

  import moment from 'moment';
  import { mapGetters, mapActions } from 'vuex';
  export default {
    name: 'Attendances',

    data() {
      return {
        pageOfItems: [],
        customLabels,
        customStyles
      }
    },

    computed: {
      ...mapGetters([ 'getAllAttendances',
                      'loadingState' ]),
    },

    methods: {
      ...mapActions([ 'fetchAttendances', 
                      'fetchAttendance',
                      'attendanceClear',
                      'formTypeChange',
                      'setLoadingState' ]),

      onChangePage(pageOfItems) {
        // update page of items
        this.pageOfItems = pageOfItems;
      },

      async newAttendance() {
        this.setLoadingState(true);
        this.formTypeChange('attendance');
        await this.attendanceClear();
      },

      async selectAttendance(attendance) {
        this.setLoadingState(true);
        await this.fetchAttendance(attendance);
        this.formTypeChange('attendance');
      },

      mapAttendances(attendance) {
        return attendance.members.reduce((a, {present}) => a + present, 0);
      },
    },

    filters: {
      formatDate: function(value) {
        if (value) {
          return moment(String(value)).format('dddd, LL')
        }
      }
    },

    async mounted() {
      await this.fetchAttendances();
      this.setLoadingState(false);
    }
  }
</script>

<style>
  ul.pagination {
    margin-top: 1em !important;
  }
  li.active {
    background: var(--gold);
    border-radius: 1em;
  }
  li:hover {
    background: var(--gold-light);
    border-radius: 1em;
  }
</style>